@import "bourbon/bourbon";

$text-color: #233542;
$text-color_hover: #0e75bb;
$border-bottom-color: #c8ccd0;

@mixin clearfix() {
  &:before,
  &:after {
    content: "";
    display: table; }

  &:after {
    clear: both; } }

@mixin placeholder-style {

  &::-webkit-input-placeholder {
    font-family: 'Klavika-regular', "PT Sans", sans-serif;
    font-size: 14px;
    font-weight: 300;
    color: rgba(255,255,255,.5); }

  &:-moz-placeholder {
    font-family: 'Klavika-regular', "PT Sans", sans-serif;
    font-size: 14px;
    font-weight: 300;
    color: rgba(255,255,255,.5); }

  &::-moz-placeholder {
    font-family: 'Klavika-regular', "PT Sans", sans-serif;
    font-size: 14px;
    font-weight: 300;
    color: rgba(255,255,255,.5); }

  &:-ms-input-placeholder {
    font-family: 'Klavika-regular', "PT Sans", sans-serif;
    font-size: 14px;
    font-weight: 300;
    color: rgba(255,255,255,.5); } }

.clearfix {
  @include clearfix(); }

*, *:hover, *:focus, *:visited {
  outline: none; }



