/* ------ Dropdown button for Navigation
  --------------------------------------- */
$w: 30px;
$h: 4px;
button.toggle_menu {
  display: none;
  width: 35px;
  height: 50px;
  position: relative;
  top: 0px;
  right: 0px;
  z-index: 100;
  border: none;
  background: transparent;
  #sandwich {
    width: $w;
    height: $w / 2;
    position: absolute;
    top: 15px;
    left: 5px;
    margin: auto;
    display: block;
    .sw-topper {
      position: absolute;
      top: 0;
      left: 0;
      width: $w;
      height: $h;
      background: #0e75bb;
      border: none;
      border-radius: 4px 4px 4px 4px;
      transition: transform 0.2s; }
    .sw-bottom {
      position: absolute;
      width: $w;
      height: $h;
      top: $h * 2;
      left: 0;
      background: #0e75bb;
      border: none;
      border-radius: 4px 4px 4px 4px;
      transition: transform 0.2s; }
    .sw-footer {
      position: absolute;
      width: $w;
      height: $h;
      top: $h * 4;
      left: 0;
      background: #0e75bb;
      border: none;
      border-radius: 4px 4px 4px 4px;
      transition: all 0.2s; } }
  #sandwich.active {
    .sw-topper {
      top: 5px;
      width: 20px;
      @include transform(rotate(140deg)); }
    .sw-bottom {
      top: 5px;
      left: 13px;
      width: 20px;
      @include transform(rotate(-140deg)); }
    .sw-footer {
      opacity: 0;
      top: 0;
      @include transform(rotate(180deg)); } } }



@media only screen and (min-width: 1000px) {
