/* ----------------------------------
  ---------- TOP SECTION OF NEW PAGE --------
  --------------------------------- */

.top-section {

  &-bg {
    position: relative;
    background-size: cover;

    &__inner {
      position: relative;
      z-index: 1;
      background-repeat: no-repeat;
      height: calc(100% - 20px); }

    &:before, &:after {
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      right: 0;
      bottom: 0; }

    &:before {
      background-image: url('./../img/new-page/triangles-for-top.png');
      background-size: 120% auto;
      background-position: 60% 5%;
      z-index: 1; }

    &:after {
      background: rgba(0, 139, 255, 0.53);
      background-size: cover;
      z-index: 0; }

    .figure {
      position: relative;
      width: 100%;
      display: flex;
      z-index: 2;
      &:after {
        position: absolute;
        content: '';
        top: 97%;
        width: 100%;
        height: 10px;
        background-color: white; }
      &-part {
        width: calc(50%);
        display: block;
        content: " ";
        height: 0;

        &_left {
          right: 50%;
          border-bottom: 20px solid white;
          border-right: 52px solid transparent; }

        &_right {
          left: 50%;
          border-bottom: 20px solid white;
          border-left: 52px solid transparent; } } } }

  &-info {
    position: relative;
    //max-width: 630px
    margin: 0 auto;
